.header {
  position: relative;
  min-height: 100vh;
  max-height: 740px;
  background: url(../images/hero-image-sm.jpg) no-repeat center / cover;
  font-family: 'Roboto', sans-serif;
  overflow: hidden;
}
@media only screen and (min-width: 768px) {
  .header {
    min-height: 555px;
    max-height: inherit;
    background: url(../images/hero-image.jpg) no-repeat center / cover;
  }
}
@media only screen and (min-width: 992px) {
  .header {
    min-height: 620px;
  }
}
@media only screen and (min-width: 1600px) {
  .header {
    min-height: 950px;
  }
}
@media only screen and (min-width: 768px) {
  .header:before {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 220px;
    height: 220px;
    background: url(../images/decor/triangle-top-left.png) no-repeat;
    background-size: contain;
  }
}
@media only screen and (min-width: 992px) {
  .header:before {
    width: 300px;
    height: 300px;
  }
}
@media only screen and (min-width: 1200px) {
  .header:before {
    width: 400px;
    height: 400px;
  }
}
@media only screen and (min-width: 1600px) {
  .header:before {
    width: 540px;
    height: 540px;
  }
}
@media only screen and (min-width: 768px) {
  .header:after {
    content: '';
    position: absolute;
    right: -150px;
    top: 0;
    width: 557px;
    height: 220px;
    background: url(../images/decor/triangle-top.png) no-repeat;
    background-size: contain;
  }
}
@media only screen and (min-width: 992px) {
  .header:after {
    width: 506px;
    height: 200px;
    right: 0;
  }
}
@media only screen and (min-width: 1200px) {
  .header:after {
    width: 633px;
    height: 250px;
  }
}
@media only screen and (min-width: 1600px) {
  .header:after {
    width: 950px;
    height: 375px;
    right: 15px;
  }
}
.header__top {
  padding: 15px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 10;
}
@media only screen and (min-width: 768px) {
  .header__top {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  display: flex;
  align-items: center;
}
.header__logo-img {
  display: block;
}
@media only screen and (min-width: 768px) {
  .header__logo-img {
    margin-right: 20px;
  }
}
.header__logo-text {
  position: relative;
  z-index: 10;
  display: none;
}
@media only screen and (min-width: 992px) {
  .header__logo-text {
    display: inherit;
  }
}
.header__link {
  position: relative;
  z-index: 10;
}
@media only screen and (min-width: 768px) {
  .header__link {
    margin-right: 25px;
    font-size: inherit;
  }
}
.header__center {
  position: relative;
  padding-top: 15vh;
  padding-bottom: 10vh;
